.nav{
    height: 40px;
   
    position: fixed;
    top:0px;
    width: 100%;
    padding: 10px;
    display: flex;
    z-index: 1;
    justify-content: space-between;
  /* Animation */
  transition-timing-function: ease-in;
  transition: all 0.5s;

}

.nav_black {
  background-color: #111;
}
.log{
    position: fixed;
    left: 20px;
    width: 80px;
    object-fit: contain;
}
.profile{
    padding-top: 5px;
    position: fixed;
    right: 20px;
    width: 30px;
    object-fit: contain;
}