.row{
    margin-left: 20px;
    color: white;
}

.row_posters{
    display: flex;
    overflow-y: hidden;
    overflow-x: scroll;
    padding: 20px;
}
.row_posters::-webkit-scrollbar{
    display: none;
}
.row_poster{
    object-fit: contain;
    width: 100%;
    max-height: 150px;
    margin-right: 10px;
    transition: transform 850;
}
.row_poster:hover{
    transform: scale(1.08); 
}
.row_posterLarge{
    max-height: 200px;
}